import $ from "../core";
import anime from "animejs/lib/anime.es.js";

$.prototype.accordion = function () {
   for (let i = 0; i < this.length; i++) {
      //     
      const headers = Array.from($(this[i]).find(".accordion-header")),
         contents = Array.from($(this[i]).find(".accordion-content")),
         inner = Array.from($(this[i]).find(".accordion-content .accordion-inner")),
         icons = Array.from($(this[i]).find(".accordion-header .icon"));

      //     
      contents.forEach((content) => {
         content.style.cssText = "overflow: hidden; max-height: auto; margin: 0; padding: 0 32px";
      });
      //      
      headers.forEach((header, j) => {
         header.addEventListener("click", () => {
            header.classList.toggle("has-text-info");
            //     
            if (inner[j].classList.contains("is-hidden")) {
               animeAccordion(inner[j], contents[j], "down"); //  
               animeAccordion(icons[j], "", "", [0, 180]); //  
            } else {
               animeAccordion(inner[j], contents[j], "up"); //  
               animeAccordion(icons[j], "", "", [180, 360]); //  
            }
         });
      });

      function animeAccordion(target, content, dir, angel) {
         const duration = 400;
         let maxHeight, py, mb;

         //   
         if (dir == "down") {
            target.classList.toggle("is-hidden"); //  
            maxHeight = ["0px", `${$(target)[0].scrollHeight + "px"}`]; //     
            py = "32px"; //    
            mb = "32px"; //   
         } else if (dir == "up") {
            maxHeight = [`${$(target)[0].scrollHeight + "px"}`, "0px"]; //     
            py = "0px"; //    
            mb = "0px"; //     
            setTimeout(() => {
               target.classList.toggle("is-hidden");
            }, duration); //    
         }

         if (!angel) {
            //  
            anime({
               targets: target,
               duration: duration,
               maxHeight: maxHeight,
               easing: "linear",
            });
            // 
            anime({
               targets: content,
               duration: duration,
               paddingTop: py,
               paddingBottom: py,
               marginBottom: mb,
               easing: "linear",
            });
         }
         //  
         if (angel) {
            anime({
               targets: target,
               duration: duration,
               rotateZ: angel,
               easing: "easeInOutSine",
            });
         }
      }
   }
};

$(".accordion").accordion();
